				"html": "<p>Objects can be thought of as a key/value storage, like a dictionary. If you have tabular data, you can use an object to \"lookup\" values rather than a <code>switch</code> statement or an <code>if/else</code> chain. This is most useful when you know that your input data is limited to a certain range.</p>\n<p>Here is an example of a simple reverse alphabet lookup:</p>\n<pre><code>var alpha = {\n  1:\"Z\",\n  2:\"Y\",\n  3:\"X\",\n  4:\"W\",\n  ...\n  24:\"C\",\n  25:\"B\",\n  26:\"A\"\n};\nalpha[2]; // \"Y\"\nalpha[24]; // \"C\"\n</code></pre>",
